@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap);
body {
  margin: 0;
  font-family: Verdana;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.App{position:absolute;height:100vh;width:100%;text-align:center;font-family:"Roboto", Verdana;font-size:1.2rem}h1{font-family:"Montserrat", "Roboto", Verdana;font-size:3rem;margin:0.5rem 0}footer{position:fixed;bottom:0;padding:1rem 0;width:100%}footer a{color:black;text-decoration:none}footer a:hover{text-decoration:underline}main{width:25rem;margin:auto;padding:1rem 0}main #time-left{margin:0;margin-bottom:1.5rem;font-family:"Lobster Two", Verdana;font-size:2.5rem}main div{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}main div div div{margin:0 .7rem;font-size:1.8rem}#start_stop,#reset,#break-decrement,#break-increment,#session-decrement,#session-increment{cursor:pointer}#start_stop:active,#reset:active,#break-decrement:active,#break-increment:active,#session-decrement:active,#session-increment:active{color:#ff0000}.buttons{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;width:10rem;margin:auto}#session-container{color:white;margin:0;margin:auto;margin-top:2rem;background-color:#242424;border-radius:1rem;width:15rem;padding-bottom:2.1rem;padding-top:0.1rem}.last-seconds{color:red}

